import React, { useEffect, useRef } from "react";
import Slider, { SliderTooltip } from "rc-slider";
import "rc-slider/assets/index.css";
import "../../../gmx-test/components/Exchange/SwapBox.scss";
import "./SwapBoxOptions.scss";

import cx from "classnames";
import { ethers, BigNumber } from "ethers";

import {
  ARBITRUM,
  expandDecimals,
  formatAmountFree,
  SWAP_OPTIONS,
  ASSET_OPTIONS,
} from "../../lib/legacyOptions";

import Tab from "../Tab/TabOptions";

import { getTokenBySymbol } from "../../config/Tokens";

import iconBTC from "../../../../img/icon-BTC.svg";
import iconETH from "../../../../img/icon-ETH.svg";

import { LyraQuoter, PRICE_CALCULATORS } from '../../../../components/utils/contracts';
import { SWAP_ICONS } from './../../lib/legacyOptions';
import { format } from "date-fns";
import NumberInput_v3 from './../../../../components/UI/input/NumberInput_v3';
import { daysToMs, fromBigNumber } from "../../../../components/utils/utils";
import { HEGIC_STRATEGIES, HEGIC_BUY_URLS } from './../../../../components/utils/contracts';
import HegicStrategy_ABI from '../../../../deployments/options/HegicStrategy_ABI.json';
import PriceCalculator_ABI from '../../../../deployments/options/PriceCalculator_ABI.json';
import Lyra from '@lyrafinance/lyra-js';
import { sepToNumber } from "../../../../components/utils/sepThousands";
import { formatAmount } from './../../../gmx-test/lib/legacy';
import LyraQuoter_ABI from '../../../../deployments/options/LyraQuoter_ABI.json';


const leverageSliderHandle = (props) => {
  const { value, dragging, index, ...restProps } = props;
  return (
    <SliderTooltip
      prefixCls="rc-slider-tooltip"
      overlay={`${value}`}
      visible={dragging}
      placement="top"
      key={index}
    >
      <Slider.Handle value={value} {...restProps} />
    </SliderTooltip>
  );
};

export default function SwapBox(props) {
  const {
    active,
    connectWallet,
    walletAddress,
    library,
    infoTokens,
    selectedToken,
    tokenSelection,
    setTokenSelection,
    assetOption,
    setAssetOption,
    swapOption,
    setSwapOption,
    leverageOption,
    setLeverageOption,
    amountValue,
    setAmountValue,
    options,
    setOptions,
    setOptionsLength,
    setOptionsExpectedLength,
    isOptionsLoading,
    setIsOptionsLoading,
    optionPrices,
    setOptionPrices,
    scrollToList,
    chaiMarkPrice,
  } = props;
  
  const isCall = swapOption === 'Call';

  const getError = () => {
    if (!amountValue) {
      return `Enter Amount`;
    }

    if (!chaiMarkPrice) {
      return `Please, wait`;
    }
  };

  const isPrimaryEnabled = () => {
    if (!active) {
      return true;
    }
    
    if (getError()) {
      return false;
    }
    
    return true;
  };


  const getPrimaryText = () => {
    if (!active) {
      return `Connect wallet`;
    }
    
    const err = getError();
    if (err) {
      return err;
    }
    
    return `See Available Options`;
  };

  const period = daysToMs(leverageOption);
  const expirationDate = period && (Date.now() + period);

  const fetchHegicOptions = () => {
    const periodDays = leverageOption;
    const periodMs = period;
    
    if (periodDays < 7 || periodDays > 90) {
      return;
    }
    
    let strategies;
    if (periodDays < 14) {
      strategies = HEGIC_STRATEGIES.short;
    } else if (periodDays < 30) {
      strategies = HEGIC_STRATEGIES.medShort;
    } else if (periodDays < 60) {
      strategies = HEGIC_STRATEGIES.medLong;
    } else if (periodDays <= 90) {
      strategies = HEGIC_STRATEGIES.long;
    }
    const expectedLength = Object.keys(strategies).filter(str => {
      return isCall ? str.includes('CALL') : !str.includes('CALL');
    }).filter(str => {
      return str.split('_')[3] === selectedToken.symbol;
    }).length;
    setOptionsExpectedLength(expectedLength);
    
    for (let i = 0; i < Object.keys(strategies).length; i++) {
      const strategy = Object.keys(strategies)[i];
      const [, strategySentiment, strikeScale, strategyTokenSymbol] = strategy.split('_');

      const strategyIsCall = strategySentiment === 'CALL';
      if (isCall && !strategyIsCall || !isCall && strategyIsCall) {
        continue;
      }
      if (strategyTokenSymbol !== selectedToken.symbol) {
        continue;
      }
      
      const strike = formatAmountFree(chaiMarkPrice, 8, 2) / 100 * strikeScale;
      
      const strategyAddress = strategies[strategy];
      const strategyContract = new ethers.Contract(strategyAddress, HegicStrategy_ABI, library.getSigner());
      const priceName = `PriceCalculator_${strategySentiment}_${strikeScale}_${strategyTokenSymbol}`;
      const priceAddress = PRICE_CALCULATORS[priceName];
      const priceContract = new ethers.Contract(priceAddress, PriceCalculator_ABI, library.getSigner());

      const urlName = `${strategySentiment}_${strikeScale}_${strategyTokenSymbol}`;

      strategyContract.getAvailableContracts(periodMs / 1000, [ethers.constants.HashZero])
        .then(res => {
          options.push({
            strategy,
            market: 'Hegic',
            type: 'American',
            isCall,
            token: strategyTokenSymbol,
            strike: expandDecimals((strike * 10**2).toFixed(0), 16),
            liq: strategyTokenSymbol === 'ETH' ? res : expandDecimals(res, 10),
            period: periodMs,
            buyUrl: HEGIC_BUY_URLS[urlName] + `&period=${periodDays}`,
          });
          setOptionsLength(options.length);
          setOptions(options);
          if (options.length === expectedLength) {
            setIsOptionsLoading(false);
          }
        })
      
      
      const decimals = strategyTokenSymbol === 'ETH'
        ? 18 : 8;

      const amountStr = String((sepToNumber(amountValue).toFixed(2)) * 10**2);
      const amount = BigNumber.from(amountStr).mul(String(10**(decimals - 2)));
      priceContract.calculatePremium(periodMs / 1000, amount, 0)
        .then(res => {
          optionPrices[strategy] = expandDecimals(res, 12);
        })
    }
  }

  const fetchLyraOptions = async () => {
    const lyra = new Lyra(ARBITRUM);
    const token = selectedToken.symbol;
    const periodMs = period;
    
    const market = (await lyra.markets()).find(mkt => {
      return mkt.baseToken.symbol === `W${token}`;
    });
    const boardId = market.liveBoards().reduce((prevBd, curBd) => {
      const prev = prevBd.expiryTimestamp * 10**3;
      const cur = curBd.expiryTimestamp * 10**3;
      const expDate = Date.now() + periodMs;

      return Math.abs(cur - expDate) < Math.abs(prev - expDate) ? curBd : prevBd;
    }).id;
    const board = market.liveBoardsMap[boardId];
    
    board.strikes.forEach(strike => {
      const { strikeId, strikePrice } = strike;
      const strategy = `Lyra_${swapOption.toUpperCase()}_${strikeId.toString()}_${token}`;
      
      options.push({
        strategy,
        market: 'Lyra',
        type: 'European',
        isCall,
        token,
        strike: strikePrice,
        strikeId,
        liq: null,
        period: board.expiry.toString() * 10**3 - Date.now(),
        buyUrl: token === 'ETH'
          ? `https://app.lyra.finance/#/trade/arbitrum/eth-usdc?expiry=${board.expiry.toString()}`
          : `https://app.lyra.finance/#/trade/arbitrum/wbtc-usdc?expiry=${board.expiry.toString()}`,
      });
      setOptionsLength(options.length);
      setOptions(options);

      const amountStr = String((sepToNumber(amountValue).toFixed(2)) * 10**2);
      const amount = BigNumber.from(amountStr).mul(String(10**16));
      lyra.quoteOption(market.address, strikeId, isCall, amount)
        .then(res => {
          const cost = res.ask.premium.add(res.ask.fee);
          optionPrices[strategy] = cost;
          
          const opt = options.find(opt => {
            return opt.strategy === strategy;
          });
          if (opt) {
            opt.liq = cost.eq(0) ? BigNumber.from(0) : amount;
          }
          
          setOptionPrices(Object.assign({}, optionPrices));
        })
    })

    setIsOptionsLoading(false);
  }

  const fetchAvailableOptions = () => {
    if (isOptionsLoading) {
      return;
    }
    
    options.splice(0, options.length);
    setOptionsLength(0);
    Object.keys(optionPrices).forEach(key => {
      optionPrices[key] = undefined;
    });
    
    setIsOptionsLoading(true);
    
    fetchHegicOptions();
    fetchLyraOptions();
  }

  const onClickPrimary = () => {
    if (!active) {
      connectWallet();
      return;
    }
    
    scrollToList();
    fetchAvailableOptions();
  };

  const onAssetOptionChange = (opt) => {
    setTokenSelection(getTokenBySymbol(ARBITRUM, opt).address);
    setAssetOption(opt);
  }
  const onSwapOptionChange = (opt) => {
    setSwapOption(opt);
  }


  const assetIcons = {
    ETH: iconETH,
    BTC: iconBTC,
  }

  if (!tokenSelection) {
    return null;
  }
  return (
    <div className={cx("Exchange-swap-box")}>
      <div className="Exchange-swap-box-inner App-box">
        <div className="Exchange-swap-box__settings">
          <Tab
            icons={SWAP_ICONS}
            options={SWAP_OPTIONS}
            option={swapOption}
            onChange={onSwapOptionChange}
            className={"Exchange-swap-option-tabs"}
          />
        </div>
        <div className="Exchange-info-row" style={{ marginBottom: 15 }}>
          <div className="Exchange-info-label">
            Asset
          </div>
          <Tab
            className="align-right"
            icons={assetIcons}
            options={ASSET_OPTIONS}
            type="inline"
            option={assetOption}
            onChange={onAssetOptionChange}
          />
        </div>
        <NumberInput_v3
          value={amountValue}
          setValue={setAmountValue}
          title="Amount"
        />
        <div className="Exchange-leverage-box">
          <div className="Exchange-leverage-slider-settings">
            <div className="Exchange-leverage-name">Period</div>
            <div className="Exchange-leverage-value">
              {leverageOption} {`Day${leverageOption !== 1 ? 's' : ''}`}
            </div>
          </div>
          <div
            className={cx("Exchange-leverage-slider", "App-slider")}
          >
            <Slider
              min={1}
              max={120}
              step={1}
              handle={leverageSliderHandle}
              onChange={(value) => setLeverageOption(value)}
              value={leverageOption}
              defaultValue={leverageOption}
            />
          </div>
          <div className="Exchange-info-row">
            <div className="Exchange-info-label">
              Expiration Date
            </div>
            <div className="align-right">
              {format(expirationDate, 'MM/dd/yy, hh:mm a')}
            </div>
          </div>
        </div>
        <div className="Exchange-swap-button-container">
          <button className={"Exchange-swap-button btn" + (isPrimaryEnabled() ? " btn_hlight" : "")} onClick={onClickPrimary} disabled={!isPrimaryEnabled()}>
            {getPrimaryText()}
          </button>
        </div>
      </div>
    </div>
  );
}
