import React, { useState, useEffect } from 'react';
import './OptionsList.scss';
import Loader from './../../../../components/UI/loader/Loader';
import OptionsItem from './OptionsItem';
import Tooltip from './../Tooltip/Tooltip';
import Chai_ABI from '../../../../deployments/options/Chai_ABI.json';
import { CHAI_CONTRACT_ETH, CHAI_CONTRACT_BTC } from '../../../../components/utils/contracts';
import useSWR from 'swr';
import { ethers } from 'ethers';
import icon_btnUp from '../../../../img/icon-btn-up.svg';
import Filter from './../../../../components/UI/Filter/Filter';
import { marketsOptions, OPTION_LIST_FRONTIER } from './../../../../components/utils/constants';
import { useLocalStorageSerializeKey, ARBITRUM } from './../../lib/legacyOptions';
import { BigNumber } from 'ethers';

const OptionsList = (props) => {
	const {
		active,
		library,
		options,
		setOptions,
		optionsExpectedLength,
		optionPrices,
		isLoading,
		scrollRef,
		swapOption,
	} = props;

	const chaiAddress = options[0]?.token === 'ETH'
    ? CHAI_CONTRACT_ETH : CHAI_CONTRACT_BTC;
  const { data: markPrice } = useSWR(
    active && [active, options[0]?.token, "getMarkPrice"],
    async () => {
      const contract = new ethers.Contract(chaiAddress, Chai_ABI, library.getSigner());
      return await contract.latestAnswer();
    }
	)

	const isCall = swapOption === 'Call';

	const listFrontierStep = OPTION_LIST_FRONTIER;
	const [listFrontier, setListFrontier] = useState(listFrontierStep);
	const [unfoldVisible, setUnfoldVisible] = useState(false);
	
	const [markets, setMarkets] = useLocalStorageSerializeKey([ARBITRUM, "Options-Markets"], marketsOptions.slice());

	const filterOptions = (arr) => {
		return arr.filter(opt => {
			for (let i = 0; i < markets.length; i++) {
				const market = markets[i];
				if (market === opt.market) {
					return true;
				}
			}
		})
	}

	const [sortType, setSortType] = useState('');
	const [sortedOptions, setSortedOptions] = useState([]);

	const sortOptions = (param, dir = 1, isInit = false) => {
		const res = filterOptions(options);
		
		const sortFn = (arr) => {
			arr.sort((a, b) => {
				const prevEl = param === 'price'
				 ? optionPrices[a.strategy]
				 : a[param];
				const el = param === 'price'
				 ? optionPrices[b.strategy]
				 : b[param];
				
				if (prevEl === null) {
					return dir;
				} else if (el === null) {
					return -dir;
				} else if (prevEl && el) {
					return 0;
				}
			})
			arr.sort((a, b) => {
				const prevEl = param === 'price'
				 ? optionPrices[a.strategy]
				 : a[param];
				const el = param === 'price'
				 ? optionPrices[b.strategy]
				 : b[param];
	
				if (!prevEl || !el) {
					return 0;
				}
				
				if (prevEl.lt(el)) {
					return dir;
				}	else if (prevEl.eq(el)) {
					return 0;
				} else {
					return -dir;
				}
			})
		}
		
		if (!isInit) {
			const [prevParam, prevDir] = sortType.split('_');
			if (param === prevParam) {
				dir = -prevDir;
			}
		} else {
			sortFn(res);
		}
		
		const arr = res.slice(0, listFrontier);
		if (!isInit) {
			sortFn(arr);
		}

		setSortedOptions(arr.slice());
		setSortType(`${param}_${dir}`);
		
		if (arr.length < res.length) {
			setUnfoldVisible(true);
		}
	}

	useEffect(() => {
		setUnfoldVisible(false);
		const [param, dir] = sortType.split('_');
		sortOptions(param, dir, true);
	}, [listFrontier, markets]);
	
	useEffect(() => {
		if (options.length) {
			setListFrontier(listFrontierStep);
			setUnfoldVisible(false);

			sortOptions('strike', isCall ? -1 : 1, true);
		} else {
			setSortedOptions([]);
		}
	}, [options.length]);

	const renderHead = () => {
		return (
			<tr className="Exchange-list-header">
				<th>
					Market
				</th>
				<th>
					<Tooltip
						className="has-hint-tooltip nowrap"
						handle="Type"
						position="left-bottom"
						enabled={true}
						renderContent={() => {
							return (
								<div className="Tooltip__text">
									American-style options may <br />exercise at any time before <br />the option expires.<br />European-style options may <br />exercise only at expiration.
								</div>
							);
						}}
					/>
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortOptions('liq');
						}}
					>
						Available
					</button>
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortOptions('strike');
						}}
					>
						Strike
					</button>
				</th>
				<th>
					Expires in
				</th>
				<th>
					Date
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortOptions('price');
						}}
					>
						Cost
					</button>
				</th>
				<th></th>
			</tr>
		)
	}

	if (!options) {
		return null;
	}
	return (
		<div className="OptionsList block" ref={scrollRef}>
			<div className="OptionsList__header">
				<h1 className='OptionsList__title'>Available Options</h1>
				<Filter
					items={marketsOptions}
					selectedItems={markets}
					setSelectedItems={setMarkets}
				/>
			</div>
			<div className="table-wrapper">
				<table className="Exchange-list large App-box">
					{renderHead()}
					<tbody>
						{(!sortedOptions.length || !markPrice) ?
							<tr>
								<td colSpan={8}>
									{
										isLoading ?
										<Loader /> :
											(options.length 
												? "Please select markets from the list above"
												: "Please choose the desired amount and period to view the available options")
									}
								</td>
							</tr>
							:
							(isLoading
								? <tr>
										<td colSpan={8}>
											<Loader />
										</td>
									</tr>
								: sortedOptions.map(opt => {
										const optPrice = optionPrices[opt.strategy];
									
										return(
											<OptionsItem
												key={opt.strategy}
												option={opt}
												optionPrice={optPrice}
												markPrice={markPrice}
											/>
										)
									}))
						}
					</tbody>
				</table>
			</div>
			{unfoldVisible &&
				<button
					className="btn btn_hlight OptionsList__unfold-btn"
					onClick={() => {
						setUnfoldVisible(false);
						setListFrontier(listFrontier + listFrontierStep);
					}}
				>
					See more
				</button>
			}
			<button className="btn_up"
				onClick={() => {
					window.scrollTo({
						top: 0,
						left: 0,
						behavior: 'smooth'
					});
				}}
			>
				<img src={icon_btnUp} alt="scroll up button" />
			</button>
		</div>
	);
};

export default OptionsList;