import React, { useState } from 'react';
import icon_curve from '../../../img/icon-market.svg';
import cx from 'classnames';
import './Filter.scss';
import icon_down from '../../../img/icon-down-small.svg';

const Filter = (props) => {
	const {
		items,
		selectedItems,
		setSelectedItems,
	} = props;

	const [open, setOpen] = useState(false);

	function openFilter(e) {
		if (open) {
			closeFilter();
			return
		}
		
		e.stopPropagation();
		document.addEventListener('click', closeFilter);
		setOpen(true);
	}
	function closeFilter() {
		document.removeEventListener('click', closeFilter);
		setOpen(false);
	}

	
	return (
		<div className={cx("Filter", open && 'active')}>
			<button className="Filter__header" onClick={openFilter}>
				<div className="Filter__header-icon">
					<img src={icon_curve} alt="Curve icon" />
				</div>
				<div className="Filter__header-title-container">
					<span className='Filter__header-title nowrap'>
						Choose Market
					</span>
					<img className='Filter__chevron-down' src={icon_down} alt="chevron down" />
				</div>
			</button>
			<div className="Filter__items" onClick={(e) => e.stopPropagation()}>
				{items.map(item => {
					const itemIcon = require(`../../../img/icon-${item}.svg`).default;
					const isSelected = selectedItems.find(el => el === item);
					
					return (
						<div className="Filter__item input-container">
							<input
								type="checkbox"
								defaultChecked={isSelected}
								onChange={(e) => {
									if (e.target.checked) {
										selectedItems.push(item);
									} else {
										const index = selectedItems.findIndex(el => el === item);
										selectedItems.splice(index, 1);
									}

									setSelectedItems(selectedItems.slice());
								}}
							/>
							<label>
								<img className='Filter__item-icon' src={itemIcon} alt={item} />
							</label>
						</div>
					)
				})}
			</div>
		</div>
	);
};

export default Filter;